export const getPeriodById = (
  allSession: SessionPlanInterface[],
  period_id: string
): SessionPlanInterface | undefined => {
  return allSession.find((item) => item.session_plan_id === period_id);
};

const searchData = (
  objList: WaitingListDataInterface[],
  text: string
): WaitingListDataInterface[] => {
  if (undefined === text || text === "") return objList;
  return objList.filter((product) => {
    let flag;
    // let dataKeys = product;

    for (let prop in product) {
      flag = false;
      if (
        prop === "names" ||
        prop === "gender" ||
        prop === "national_id" ||
        prop === "phone" ||
        prop === "email" ||
        prop === "user_position_marks" ||
        prop === "position_title" ||
        prop === "education_category" ||
        prop === "waitingList_session" ||
        prop === "date" ||
        prop === "waiting_status"
      ) {
        if (product[prop]) {
          flag =
            product[prop]!.toString()
              .toLowerCase()
              .indexOf(text.toLowerCase()) > -1;
        }
      }
      if (flag) break;
    }
    return flag;
  });
};

const defaultReportContent: waitingListReportContentInterface[] = [
  {
    key: waitingListReportContentKeyInterface.names,
    title: "Applicant Names",
    status: true,
  },
  {
    key: waitingListReportContentKeyInterface.national_id,
    title: "National ID",
    status: true,
  },
  {
    key: waitingListReportContentKeyInterface.gender,
    title: "Gender",
    status: true,
  },
  {
    key: waitingListReportContentKeyInterface.phone,
    title: "Phone",
    status: true,
  },
  {
    key: waitingListReportContentKeyInterface.user_position_marks,
    title: "Applicant marks",
    status: true,
  },
  {
    key: waitingListReportContentKeyInterface.waitingList_session,
    title: "Waiting list Session",
    status: true,
  },
  {
    key: waitingListReportContentKeyInterface.education_category,
    title: "Education Institute",
    status: true,
  },
  {
    key: waitingListReportContentKeyInterface.date,
    title: "Waiting list Date",
    status: true,
  },
  {
    key: waitingListReportContentKeyInterface.waiting_status,
    title: "Waiting list Status",
    status: true,
  },
  {
    key: waitingListReportContentKeyInterface.position_title,
    title: "Position",
    status: true,
  },
];

type AppProps = {
  auth: Auth;
  application_sessions: SessionPlanInterface[] | null;
  systemPositions: PositionAPI[] | null;
  schoolLevels: schoolLevelsStoreInterface;
  waitingList: WaitingListInterface[] | null;
  getWaitingList: Function;
  getPositions: Function;
  GetAllSessionPlans: Function;
  loadEducationCategories: (callback: (loading: boolean) => void) => void;
  PushPopupAlert: (alert: PopupAlertData) => void;
};

type AppState = {
  thisPeriod: SessionPlanInterface | "all";
  loading: boolean;
  loadingWaitingList: boolean;
  waitingListToPrint: WaitingListToPrintInterface[] | null;
  thisWaitingListStatus: WaitingListStatus | "all";
  thisPosition: PositionAPI | null;
  selectPosition: boolean;
  sortProperty: string;
  isSorted: boolean;
  sortOrder: "ASC" | "DESC";
  searchInput: string;
  searchPositionInput: string;
  downloading: boolean;
  generateReportModal: {
    reportType: "EXCEL" | "PDF";
    status: boolean;
  } | null;
  reportTitle: string;
  reportContent: waitingListReportContentInterface[];
  selectAll: boolean;
  formError: {
    element: string;
    msg: string;
  } | null;
  selectPeriod: boolean;
  thisEducation: education_category | "all";
};

class _WaitingListReport extends Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      thisPeriod: "all",
      loading: true,
      loadingWaitingList: true,
      waitingListToPrint: null,
      thisWaitingListStatus: "all",
      thisPosition: null,
      selectPosition: false,
      sortProperty: "",
      isSorted: false,
      sortOrder: "ASC",
      searchInput: "",
      searchPositionInput: "",
      downloading: false,
      generateReportModal: null,
      reportTitle: "",
      reportContent: defaultReportContent,
      selectAll: true,
      formError: null,
      selectPeriod: false,
      thisEducation: "all",
    };
  }

  setLoading = (value: boolean) => {
    this.setState({ loading: value });
  };

  setLoadingWaitingList = (value: boolean) => {
    this.setState({ loadingWaitingList: value });
  };

  setThisPeriod = (value: SessionPlanInterface | "all") => {
    this.setState({ thisPeriod: value });
  };

  setWaitingListToPrint = (value: WaitingListToPrintInterface[] | null) => {
    this.setState({ waitingListToPrint: value });
  };

  setThisWaitingListStatus = (value: WaitingListStatus | "all") => {
    this.setState({ thisWaitingListStatus: value });
  };

  setThisPosition = (value: PositionAPI | null) => {
    this.setState({ thisPosition: value });
  };

  setSelectPosition = (value: boolean) => {
    this.setState({ selectPosition: value });
  };

  setSortProperty = (value: string | "all") => {
    this.setState({ sortProperty: value });
  };

  setIsSorted = (value: boolean) => {
    this.setState({ isSorted: value });
  };

  setSortOrder = (value: "ASC" | "DESC") => {
    this.setState({ sortOrder: value });
  };

  setSearchInput = (value: string) => {
    this.setState({ searchInput: value });
  };

  setSearchPositionInput = (value: string) => {
    this.setState({ searchPositionInput: value });
  };

  setDownloading = (value: boolean) => {
    this.setState({ downloading: value });
  };

  setGenerateReportModal = (
    value: {
      reportType: "EXCEL" | "PDF";
      status: boolean;
    } | null
  ) => {
    this.setState({ generateReportModal: value });
  };

  setReportTitle = (value: string) => {
    this.setState({ reportTitle: value });
  };

  setReportContent = (value: waitingListReportContentInterface[]) => {
    this.setState({ reportContent: value });
  };

  setSelectAll = (value: boolean) => {
    this.setState({ selectAll: value });
  };

  setFormError = (
    value: {
      element: string;
      msg: string;
    } | null
  ) => {
    this.setState({ formError: value });
  };

  setSelectPeriod = (value: boolean) => {
    this.setState({ selectPeriod: value });
  };

  setThisEducation = (value: education_category | "all") => {
    this.setState({ thisEducation: value });
  };

  componentDidMount = () => {
    // LOADING EDUCATION CATEGORY =================================
    if (
      this.props.schoolLevels.education_categories === null ||
      this.props.schoolLevels.education_categories.length === 0
    ) {
      this.props.loadEducationCategories(this.setLoading);
    }
    // END LOADING EDUCATION CATEGORY =================================
    // ###################################################################
    // LOADING APPLICATION SESSION ====================================
    if (this.props.application_sessions === null) {
      this.props.GetAllSessionPlans((status: boolean, msg: string) => {
        this.setLoading(status);
        if (!status) {
          if (msg.length) {
            this.props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        }
      });
    }
    // END LOADING APPLICATION SESSION ====================================
    // ###################################################################
    // LOADING POSITIONS ====================================
    if (this.props.systemPositions === null) {
      this.props.getPositions((status: boolean, msg: string) => {
        this.setLoading(status);
        if (msg.length) {
          this.props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        }
      });
    }

    // LOADING WAITING LIST ========================================
    if (this.state.loadingWaitingList) {
      this.props.getWaitingList((status: boolean, msg: string) => {
        this.setLoadingWaitingList(status);
        if (status === false) {
          if (msg.length > 0) {
            this.props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        }
      });
    }
    // END LOADING CLAIM TYPES ========================================
  };

  setSort = (thisProperty: string) => {
    if (
      this.state.sortProperty === thisProperty &&
      this.state.sortOrder === "ASC"
    ) {
      this.setSortOrder("DESC");
    } else {
      this.setSortOrder("ASC");
    }
    this.setSortProperty(thisProperty);
    this.setIsSorted(true);
  };

  showStatus = (status: WaitingListStatus | "all"): string => {
    let result: string = "";
    switch (status) {
      case WaitingListStatus.WAITING:
        result = "Waiting";
        break;
      case WaitingListStatus.PENDING:
        result = "Placed & pending";
        break;
      case WaitingListStatus.PLACED:
        result = "Placed & confirmed";
        break;
      default:
        result = "All";
        break;
    }

    return result;
  };

  FC_WaitingListStatus = (
    WaitingList: WaitingListDataInterface[]
  ): {
    title: string;
    count: number;
    type: WaitingListStatus | "all";
  }[] => {
    let output: {
      title: string;
      count: number;
      type: WaitingListStatus | "all";
    }[] = [{ title: "All status", count: WaitingList.length, type: "all" }];
    const statusArray = Object.values(WaitingListStatus);
    for (let i in statusArray) {
      output.push({
        title: capitalizeFirstLetter(statusArray[i]),
        count: WaitingList.filter((itm) => itm.status === statusArray[i])
          .length,
        type: statusArray[i],
      });
    }
    return output;
  };

  updateReportContent = (key: waitingListReportContentKeyInterface) => {
    let index: number = this.state.reportContent.findIndex(
      (item) => item.key === key
    );
    let newArray: waitingListReportContentInterface[] =
      this.state.reportContent;
    newArray[index].status = !newArray[index].status;
    this.setReportContent(newArray);
    let isSelectAllNeeded = newArray.find((item) => item.status === false);
    if (isSelectAllNeeded) {
      this.setSelectAll(false);
    } else {
      this.setSelectAll(true);
    }

    // UPDATE APPLICATION LIST FOR UPDATE ==============================
    this.updateWaitingListToPrint();
  };

  updateWaitingListToPrint = () => {
    // UPDATE REPORT TITLE ========================
    if (this.state.thisPosition) {
      let session: string = "";
      if (this.state.thisPeriod === "all") {
        session = "All session";
      } else {
        session = this.state.thisPeriod.session_plan_name;
      }
      this.setReportTitle(
        "Waiting list - " +
          this.state.thisPosition.position_title +
          " - " +
          session
      );
    }
    // UPDATE CONTENT =============================
    if (
      this.props.systemPositions &&
      this.props.waitingList &&
      this.props.application_sessions
    ) {
      let arrayNew: WaitingListToPrintInterface[] = [];
      let n: number = 1;
      for (const key in this.props.waitingList) {
        if (Object.prototype.hasOwnProperty.call(this.props.waitingList, key)) {
          const element = this.props.waitingList[key];
          const thisPosition = getPositionById(
            this.props.systemPositions,
            element.position_id
          );
          const thisSession = getPeriodById(
            this.props.application_sessions,
            element.session_id
          );
          let dataArray: WaitingListToPrintInterface = {
            id: n.toString(),
            names: element.user_info.fname + " " + element.user_info.lname,
            gender: element.user_info.sex === "M" ? "Male" : "Female",
            national_id: element.n_id,
            phone: element.phone_number,
            email: element.email,
            user_position_marks: Number(element.marks.toFixed(2)),
            position_title: thisPosition
              ? thisPosition.position_title
              : "Unknown position",
            education_category: thisPosition
              ? thisPosition.education_institute_id.education_category_title
              : "-",
            waitingList_session: thisSession
              ? thisSession.session_plan_name
              : "Unknown session",
            date: DATE(element.createdAt),
            waiting_status: this.showStatus(element.status),
          };

          for (const key in this.state.reportContent) {
            if (
              Object.prototype.hasOwnProperty.call(
                this.state.reportContent,
                key
              )
            ) {
              if (!this.state.reportContent[key].status) {
                delete dataArray[this.state.reportContent[key].key];
              }
            }
          }
          arrayNew.push(dataArray);
          n++;
        }
      }
      this.setWaitingListToPrint(arrayNew);
      arrayNew = [];
      n = 1;
    }
  };

  FC_WaitingListData = (
    allWaitList: WaitingListInterface[] | null,
    systemPositions: PositionAPI[] | null
  ): WaitingListDataInterface[] | null => {
    if (
      allWaitList === null ||
      systemPositions === null ||
      this.props.application_sessions === null
    )
      return null;
    let arrayNew: WaitingListDataInterface[] = [];
    // LOOP IN ALL WAITING LIST ONE BY ONE ###########################
    for (const key in allWaitList) {
      const element = allWaitList[key];
      if (element.user_info) {
        const thisPosition = getPositionById(
          systemPositions,
          element.position_id
        );
        const thisSession = getPeriodById(
          this.props.application_sessions,
          element.session_id
        );
        let dataArray: WaitingListDataInterface = {
          names: element.user_info.fname + " " + element.user_info.lname,
          waitingList_id: element._id,
          gender:
            element.user_info.sex === "M"
              ? applicantGender.male
              : applicantGender.male,
          national_id: element.n_id,
          phone: element.phone_number,
          email: element.email,
          user_position_marks: Number(element.marks.toFixed(2)),
          position_id: thisPosition
            ? thisPosition.position_id
            : "Unknown position",
          position_title: thisPosition
            ? thisPosition.position_title
            : "Unknown position",
          education_category: thisPosition
            ? thisPosition.education_institute_id.education_category_title
            : "",
          education_category_id: thisPosition
            ? thisPosition.education_institute_id.education_institute_id
            : "",
          waitingList_session: thisSession
            ? thisSession.session_plan_name
            : "Unknown session",
          date: DATE(element.createdAt),
          waiting_status: this.showStatus(element.status),
          status: element.status,
          waitingList_session_id: element.session_id,
        };
        arrayNew.push(dataArray);
      }
    }
    return arrayNew;
  };

  resetReportContent = (currentState: boolean) => {
    let newArray: waitingListReportContentInterface[] =
      this.state.reportContent;
    for (const key in newArray) {
      if (this.isChangeAllowed(newArray[key].key)) {
        newArray[key].status = !currentState;
      }
    }

    this.setReportContent(newArray);
    this.setSelectAll(!currentState);
    this.updateWaitingListToPrint();
  };

  isChangeAllowed = (key: waitingListReportContentKeyInterface): boolean => {
    if (
      key === waitingListReportContentKeyInterface.names ||
      key === waitingListReportContentKeyInterface.waiting_status ||
      key === waitingListReportContentKeyInterface.waitingList_session ||
      key === waitingListReportContentKeyInterface.position_title ||
      key === waitingListReportContentKeyInterface.date ||
      key === waitingListReportContentKeyInterface.user_position_marks ||
      key === waitingListReportContentKeyInterface.national_id ||
      key === waitingListReportContentKeyInterface.education_category
    ) {
      return false;
    }
    return true;
  };

  generateReportTitle = (position_title: string) => {
    let session: string = "";
    if (this.state.thisPeriod === "all") {
      session = "All session";
    } else {
      session = this.state.thisPeriod.session_plan_name;
    }
    this.setReportTitle("Waiting list - " + position_title + " - " + session);
  };

  FC_GeneratePDF = () => {
    if (!document.querySelectorAll("#report_content tbody tr").length) {
      this.props.PushPopupAlert({
        title: `Can't generate Report of empty Data`,
        theme: PopupAlertTheme.warning,
      });
      return;
    }

    let session: string = "";
    if (this.state.thisPeriod === "all") {
      session = "All session";
    } else {
      session = this.state.thisPeriod.session_plan_name;
    }
    const table: HTMLElement | null = document.getElementById("report_content");
    const reportSettings: reportSettingsInterface = {
      doneBy: this.props.auth.user.fname + " " + this.props.auth.user.lname,
      title: this.state.reportTitle,
      subjectTitle: "Position",
      subject: this.state.thisPosition
        ? this.state.thisPosition.position_title
        : "All position",
      period: session,
      footer: "Online Teacher Recruitment",
      institute: this.state.thisPosition
        ? this.state.thisPosition.education_institute_id
            .education_category_title
        : "",
    };
    table && generatePDF(reportSettings, table, this.setDownloading);
  };

  FC_FilterWaitingList = (
    allWaitList: WaitingListDataInterface[],
    waitingListStatus: WaitingListStatus | "all",
    thisSession: SessionPlanInterface | "all",
    institute: education_category | "all",
    position: PositionAPI | null
  ): WaitingListDataInterface[] => {
    let result: WaitingListDataInterface[] = allWaitList;
    if (waitingListStatus !== "all") {
      result = result.filter((item) => item.status === waitingListStatus);
    }

    if (position) {
      result = result.filter(
        (item) => item.position_id === position.position_id
      );
    }

    if (thisSession !== "all") {
      result = result.filter(
        (item) => item.waitingList_session_id === thisSession.session_plan_id
      );
    }

    if (institute !== "all") {
      result = result.filter(
        (item) => item.education_category === institute.education_category_title
      );
    }

    return result;
  };

  render() {
    if (
      this.props.application_sessions &&
      this.props.application_sessions.length === 0
    ) {
      return <NoSession />;
    }

    return (
      <div>
        <SideNavigation>
          <div className="flex flex-col mb-4">
            <span className="text-sm text-gray-600 mb-2">
              Education categories:
            </span>
            {this.props.schoolLevels.education_categories === null ? (
              <Loading />
            ) : this.props.schoolLevels.education_categories.length === 0 ? (
              <span>No institute found</span>
            ) : (
              <Fragment>
                <div
                  onClick={() => this.setThisEducation("all")}
                  className={`border rounded px-2 py-2 mb-2 cursor-pointer ${
                    this.state.thisEducation === "all"
                      ? "bg-blue-600 text-white hover:bg-blue-100 hover:text-blue-600 hover:border-blue-500"
                      : "hover:bg-blue-100 hover:text-blue-600 hover:border-blue-500"
                  } font-bold flex flex-row items-center gap-2`}
                >
                  {this.state.thisEducation === "all" ? (
                    <MdRadioButtonChecked />
                  ) : (
                    <MdRadioButtonUnchecked />
                  )}
                  <span>All</span>
                </div>
                {this.props.schoolLevels.education_categories.map((item, i) => (
                  <div
                    onClick={() => this.setThisEducation(item)}
                    key={i + 1}
                    className={`border rounded px-2 py-2 mb-2 cursor-pointer ${
                      this.state.thisEducation !== "all" &&
                      this.state.thisEducation.education_institute_id ===
                        item.education_institute_id
                        ? "bg-blue-600 text-white hover:bg-blue-100 hover:text-blue-600 hover:border-blue-500"
                        : "hover:bg-blue-100 hover:text-blue-600 hover:border-blue-500"
                    } font-bold flex flex-row items-center gap-2`}
                  >
                    {this.state.thisEducation !== "all" &&
                    this.state.thisEducation.education_institute_id ===
                      item.education_institute_id ? (
                      <MdRadioButtonChecked />
                    ) : (
                      <MdRadioButtonUnchecked />
                    )}
                    <span>{item.education_category_title}</span>
                  </div>
                ))}
              </Fragment>
            )}
          </div>
          <div className="">
            <div className="text-gray-600 text-sm mb-2">Selected period:</div>
            <div
              onClick={() => this.setSelectPeriod(true)}
              className="font-bold bg-blue-50 border border-blue-200 rounded py-2 cursor-pointer text-base px-2 hover:underline  text-blue-600  mb-4 w-full flex flex-row items-center gap-2 justify-between animate__animated animate__zoomIn"
            >
              {this.state.thisPeriod !== "all"
                ? `${this.state.thisPeriod.session_plan_name} - ${this.state.thisPeriod.period}`
                : "All session"}
              <FiEdit className="text-xl text-blue-600" />
            </div>
          </div>

          <div className="pt-1">
            <div className="text-sm text-gray-700 pb-2 pt-2">Claim types:</div>
            {this.state.loadingWaitingList ||
            this.FC_WaitingListData(
              this.props.waitingList,
              this.props.systemPositions
            ) === null ? (
              <Loading />
            ) : (
              this.FC_WaitingListStatus(
                this.FC_WaitingListData(
                  this.props.waitingList,
                  this.props.systemPositions
                )!
              ).map((item, index: number) => (
                <div
                  onClick={() => this.setThisWaitingListStatus(item.type)}
                  key={item.title + index}
                >
                  <MainItem
                    title={item.title}
                    count={item.count}
                    active={this.state.thisWaitingListStatus === item.type}
                  />
                </div>
              ))
            )}
          </div>
        </SideNavigation>

        <section>
          {/* REPORT TITLE ============================================ */}
          <div className="grid md:grid-cols-12">
            <div className="col-span-6">
              <div className="mt-4 mx-4 flex flex-col gap-1">
                <span className="text-3xl font-bold text-gray-700">
                  Waiting list report
                </span>
                <div
                  onClick={() => this.setSelectPosition(true)}
                  className="flex flex-row gap-2"
                >
                  <span className="text-gray-500">Selected position: </span>
                  <span className="text-blue-600 font-bold underline cursor-pointer">
                    {this.state.thisPosition !== null
                      ? this.state.thisPosition.position_title
                      : "All position"}
                  </span>
                </div>
              </div>
            </div>
            <div className="col-span-6">
              <div className="float-right mt-2">
                {this.props.waitingList && this.props.waitingList.length > 0 && (
                  <div className="flex flex-row items-center justify-end font-bold mb-2">
                    <BiExport className="text-2xl text-gray-500" />
                    <span className="text-gray-500">Export as</span>
                    <button
                      className="px-4 py-1 ml-1 rounded bg-white border hover:text-blue-600 font-bold hover:bg-blue-50 border-gray-200"
                      disabled={this.state.loading}
                      onClick={() => {
                        this.updateWaitingListToPrint();
                        this.setGenerateReportModal({
                          reportType: "EXCEL",
                          status: true,
                        });
                      }}
                    >
                      EXCEL
                    </button>
                    <button
                      className={`px-4 py-1 ml-1 rounded bg-white border hover:text-blue-600 font-bold hover:bg-blue-50 border-gray-200 ${
                        this.state.downloading && "cursor-wait"
                      }`}
                      onClick={() => this.FC_GeneratePDF()}
                      disabled={this.state.loading || this.state.downloading}
                    >
                      PDF
                    </button>
                  </div>
                )}
                <div
                  onClick={() => this.setSelectPosition(true)}
                  className="btn bg-blue-50 border border-blue-300 text-blue-600 cursor-pointer hover:bg-blue-100 flex flex-row items-center justify-center gap-2 text-sm "
                >
                  <BiFilterAlt className="text-xl" />
                  Filter by position
                </div>
              </div>
            </div>
          </div>
          {/* END REPORT TITLE ============================================ */}

          <div className="bg-white my-4">
            <div className="w-full px-2 py-2">
              <input
                onChange={(e) => this.setSearchInput(e.target.value)}
                type="text"
                value={this.state.searchInput}
                placeholder="Search..."
                className="w-full bg-gray-100 py-2 px-3 rounded focus:border-gray-800"
                disabled={
                  this.state.loadingWaitingList ||
                  this.FC_WaitingListData(
                    this.props.waitingList,
                    this.props.systemPositions
                  ) === null
                }
              />
            </div>
            <div className="w-full overflow-x-auto">
              <table className="w-full shadow" id="report_content">
                <thead className="bg-white border-b-2 border-gray-300 text-gray-600 font-bold text-sm">
                  <tr>
                    <td className="px-2 py-2">#</td>
                    <td
                      className="px-2 py-2 cursor-pointer"
                      onClick={() => this.setSort("names")}
                    >
                      <div className="flex items-center gap-1">
                        Applicant names <BiSort />
                      </div>
                    </td>
                    <td className="px-2 py-2">National ID</td>
                    <td
                      className="px-2 py-2 cursor-pointer"
                      onClick={() => this.setSort("gender")}
                    >
                      <div className="flex items-center gap-1">
                        Gender <BiSort />
                      </div>
                    </td>
                    <td className="px-2 py-2">Phone</td>
                    <td
                      className="px-2 py-2 cursor-pointer"
                      onClick={() => this.setSort("user_position_marks")}
                    >
                      <div className="flex items-center gap-1">
                        Marks <BiSort />
                      </div>
                    </td>
                    <td
                      className="px-2 py-2 cursor-pointer"
                      onClick={() => this.setSort("position_title")}
                    >
                      <div className="flex items-center gap-1">
                        Position <BiSort />
                      </div>
                    </td>
                    <td
                      className="px-2 py-2 cursor-pointer"
                      onClick={() => this.setSort("education_category")}
                    >
                      <div className="flex items-center gap-1">
                        Institute <BiSort />
                      </div>
                    </td>
                    <td
                      className="px-2 py-2 cursor-pointer"
                      onClick={() => this.setSort("waiting_status")}
                    >
                      <div className="flex items-center gap-1">
                        Status <BiSort />
                      </div>
                    </td>
                    <td
                      className="px-2 py-2 cursor-pointer"
                      onClick={() => this.setSort("waitingList_session")}
                    >
                      <div className="flex items-center gap-1">
                        Session <BiSort />
                      </div>
                    </td>
                    <td
                      className="px-2 py-2 cursor-pointer"
                      onClick={() => this.setSort("date")}
                    >
                      <div className="flex items-center gap-1">
                        Date <BiSort />
                      </div>
                    </td>
                  </tr>
                </thead>
                <tbody>
                  {this.state.loadingWaitingList ? (
                    <TableLoading cols={11} />
                  ) : this.FC_WaitingListData(
                      this.props.waitingList,
                      this.props.systemPositions
                    ) === null ? (
                    <TableLoading cols={10} />
                  ) : this.FC_FilterWaitingList(
                      this.FC_WaitingListData(
                        this.props.waitingList,
                        this.props.systemPositions
                      )!,
                      this.state.thisWaitingListStatus,
                      this.state.thisPeriod,
                      this.state.thisEducation,
                      this.state.thisPosition
                    ).length === 0 ? (
                    <tr>
                      <td colSpan={11} className="px-2 py-2">
                        <div className="flex flex-col text-gray-400 items-center justify-center bg-gray-100 rounded py-10">
                          <ImFilesEmpty className="text-8xl text-gray-400 mb-2 animate__animated animate__shakeX" />
                          <span className="text-sm font-bold">
                            No result found! Please try to search again
                          </span>
                          <div className="w-full mt-2 flex flex-row items-center justify-center">
                            <ul className="text-sm py-2 text-blue-600 font-bold flex flex-row gap-2">
                              <li
                                onClick={() => this.setSelectPosition(true)}
                                className="underline text-gray-500 cursor-pointer"
                              >
                                Filter by position
                              </li>
                              <li className="text-gray-400 italic">or</li>
                              <li
                                className="text-gray-500 underline"
                                onClick={() => this.setSelectPeriod(true)}
                              >
                                Filter by period
                              </li>
                            </ul>
                          </div>
                        </div>
                      </td>
                    </tr>
                  ) : (
                    sortThisArray(
                      this.state.isSorted,
                      searchData(
                        this.FC_FilterWaitingList(
                          this.FC_WaitingListData(
                            this.props.waitingList,
                            this.props.systemPositions
                          )!,
                          this.state.thisWaitingListStatus,
                          this.state.thisPeriod,
                          this.state.thisEducation,
                          this.state.thisPosition
                        ),
                        this.state.searchInput
                      ),
                      this.state.sortProperty,
                      this.state.sortOrder
                    ).map((item: WaitingListDataInterface, i: number) => (
                      <tr
                        key={i + 1}
                        className={`${
                          i % 2 !== 0 ? "bg-gray-100" : "bg-white"
                        } text-gray-600 hover:bg-blue-100 hover:text-blue-700 cursor-pointer border-b border-gray-300 hover:border-blue-500 text-sm`}
                      >
                        <td className="px-2 py-2">{i + 1}</td>
                        <td className="px-2 py-2">{item.names}</td>
                        <td className="px-2 py-2">{item.national_id}</td>
                        <td className="px-2 py-2">{item.gender}</td>
                        <td className="px-2 py-2">{item.phone}</td>
                        <td className="px-2 py-2">
                          {item.user_position_marks}
                        </td>
                        <td className="px-2 py-2">{item.position_title}</td>
                        <td className="px-2 py-2">{item.education_category}</td>
                        <td className="px-2 py-2">
                          {capitalizeFirstLetter(
                            item.waiting_status.toLowerCase()
                          )}
                        </td>
                        <td className="px-2 py-2">
                          {item.waitingList_session}
                        </td>
                        <td className="px-2 py-2">{item.date}</td>
                      </tr>
                    ))
                  )}
                </tbody>
              </table>
            </div>
          </div>

          {/* CHOOSING POSITION MODAL */}
          {this.state.selectPosition && (
            <Modal
              title={
                <div className="flex flex-row gap-4 items-center">
                  <IoFilter className="text-5xl text-blue-600" />
                  <div className="flex flex-col">
                    <span className="font-bold text-xl text-blue-600">
                      Filter by a position
                    </span>
                  </div>
                </div>
              }
              backDrop={true}
              theme={Themes.default}
              close={() => {
                this.setSelectPosition(false);
                this.setSearchPositionInput("");
              }}
              backDropClose={true}
              widthSizeClass={ModalSize.extraLarge}
              displayClose={true}
              padding={{ title: true }}
            >
              <div className="px-2 pb-2">
                {this.props.systemPositions === null ? (
                  <LoadingV2 type={LoadingType.dark} />
                ) : this.props.systemPositions.length === 0 ? (
                  <div className="flex flex-col text-gray-400 items-center justify-center">
                    <MdSearch className="text-6xl" />
                    <div className="flex flex-col items-center justify-center">
                      <span>No positions found! Please go back!</span>
                    </div>
                  </div>
                ) : (
                  <>
                    <div className="flex flex-col">
                      <span className="text-gray-600 text-xs font-bold">
                        Filter by position name
                      </span>
                      <input
                        onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
                          this.setSearchPositionInput(e.target.value)
                        }
                        type="text"
                        className="mb-4 bg-gray-100 px-3 py-2 rounded"
                        placeholder="Keyword..."
                      />
                    </div>
                    <div
                      onClick={() => {
                        this.generateReportTitle("All position");
                        this.setThisPosition(null);
                        this.setSelectPosition(false);
                        this.setSearchPositionInput("");
                      }}
                      className="bg-white cursor-pointer rounded  px-3 py-2 border border-gray-200 hover:border-blue-500 hover:bg-blue-100 mb-2"
                    >
                      <div className="grid lg:grid-cols-12">
                        <div className="col-span-12 lg:col-span-10">
                          <div className="flex flex-row items-center gap-3">
                            <div>
                              <IoIosBriefcase className="text-4xl md:text-5xl text-blue-600" />
                            </div>
                            <div className="flex flex-col">
                              <span className="text-base lg:text-lg font-bold text-gray-700">
                                All position
                              </span>
                            </div>
                          </div>
                        </div>
                        <div className="col-span-12 lg:col-span-2 hidden lg:block">
                          <div className="h-full flex items-center justify-end">
                            <div className="btn bg-blue-600 text-white float-right">
                              Choose
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                    {search(
                      this.props.systemPositions,
                      this.state.searchPositionInput
                    ).map((item: PositionAPI, key: number) => (
                      <div
                        key={key + 1}
                        onClick={() => {
                          this.generateReportTitle(item.position_title);
                          this.setThisPosition(item);
                          this.setSelectPosition(false);
                          this.setSearchPositionInput("");
                        }}
                        className="bg-white cursor-pointer rounded  px-3 py-2 border border-gray-200 hover:border-blue-500 hover:bg-blue-100 mb-2"
                      >
                        <div className="grid lg:grid-cols-12">
                          <div className="col-span-12 lg:col-span-10">
                            <div className="flex flex-row items-center gap-3">
                              <div>
                                <IoIosBriefcase className="text-4xl md:text-5xl text-blue-600" />
                              </div>
                              <div className="flex flex-col">
                                <span className="text-xs lg:text-sm font-bold text-gray-700">
                                  {item.position_title}
                                </span>
                                <span className="text-xs text-gray-500 italic">
                                  School level:{" "}
                                  <span
                                    className="text-gray-800 font-light"
                                    style={{ fontStyle: "normal" }}
                                  >
                                    {item.school_level_id.school_level_name}
                                  </span>
                                </span>
                                <span className="text-xs font-bold text-blue-600 bg-blue-100 w-max px-3 py-1 rounded-full">
                                  {
                                    item.education_institute_id
                                      .education_category_title
                                  }
                                </span>
                              </div>
                            </div>
                          </div>
                          <div className="col-span-12 lg:col-span-2 hidden lg:block">
                            <div className="h-full flex items-center justify-end">
                              <div className="btn bg-blue-600 text-white float-right">
                                Choose
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    ))}
                  </>
                )}
              </div>
            </Modal>
          )}
          {/* END CHOOSING POSITION MODAL */}

          {/* ####################################################################################### */}

          {/* CHOOSING PERIOD CONTENT MODAL */}
          {this.state.selectPeriod && (
            <Modal
              title={
                <div className="flex flex-row gap-4 items-center">
                  <MdHistory className="text-5xl text-blue-600" />
                  <span className="flex flex-col">Choose a period</span>
                </div>
              }
              backDrop={true}
              theme={Themes.default}
              close={() => this.setSelectPeriod(false)}
              backDropClose={true}
              widthSizeClass={ModalSize.extraLarge}
              displayClose={true}
              padding={{ title: true }}
            >
              <ChooseSession
                allSession={this.props.application_sessions}
                setSelectSession={(value: SessionPlanInterface | "all") => {
                  this.setThisPeriod(value);
                  this.setSelectPeriod(false);
                }}
                chooseAll={true}
                currentSelected={this.state.thisPeriod}
              />
            </Modal>
          )}
          {/* END CHOOSING PERIOD CONTENT MODAL */}

          {/* ####################################################################################### */}

          {/* GENERATE DYNAMIC REPORT CONTENT MODAL */}
          {this.state.generateReportModal !== null &&
            this.state.generateReportModal.status === true && (
              <Modal
                title={
                  <div className="flex flex-row gap-4 items-center">
                    {/* <IoBriefcase className="text-5xl text-gray-500" /> */}
                    <div className="flex flex-col">
                      <span className="font-bold text-2xl text-gray-500">
                        Generating Report contents
                      </span>
                      <span className="text-sm text-blue-600 font-normal">
                        Generating {this.state.generateReportModal.reportType}{" "}
                        Report
                      </span>
                    </div>
                  </div>
                }
                backDrop={true}
                theme={Themes.default}
                close={() => this.setGenerateReportModal(null)}
                backDropClose={false}
                widthSizeClass={ModalSize.medium}
                displayClose={true}
                padding={{ title: true }}
              >
                <div className="px-6">
                  <div className="my-2 text-base text-gray-700 sm:text-lg">
                    <div className="flex flex-col">
                      <label>Title</label>
                      <input
                        type="text"
                        className={`text-left input-white w-20 inline-block ${
                          this.state.formError &&
                          this.state.formError.element === "report_title"
                            ? "border-red-500"
                            : "border-gray-500"
                        } border-2`}
                        placeholder="Sub Category Title"
                        value={this.state.reportTitle}
                        onChange={(e) => {
                          this.setFormError(null);
                          this.setReportTitle(e.target.value);
                        }}
                        disabled={this.state.loading}
                      />
                      {this.state.formError &&
                        this.state.formError.element === "report_title" && (
                          <span className="text-red-500">
                            {this.state.formError.msg}
                          </span>
                        )}
                    </div>
                    <div className="flex flex-col mt-2">
                      <label>Report content</label>
                      <div className="grid grid-cols-2 md:grid-cols-3 items-center gap-2 mt-2">
                        {this.state.reportContent.map((item, index) => (
                          <div
                            key={index}
                            className={`flex items-center gap-1 ${
                              this.isChangeAllowed(item.key)
                                ? "cursor-pointer"
                                : "cursor-not-allowed"
                            }`}
                            onClick={() =>
                              this.isChangeAllowed(item.key) &&
                              this.updateReportContent(item.key)
                            }
                            title={
                              !this.isChangeAllowed(item.key)
                                ? "Required for every report!"
                                : ""
                            }
                          >
                            {item.status ? (
                              <MdCheckBox className="w-6 h-6 text-blue-700" />
                            ) : (
                              <MdCheckBoxOutlineBlank className="w-6 h-6" />
                            )}
                            <span>{item.title}</span>
                          </div>
                        ))}
                      </div>
                      {this.state.formError &&
                        this.state.formError.element === "report_content" && (
                          <span className="text-red-500">
                            {this.state.formError.msg}
                          </span>
                        )}
                    </div>
                  </div>
                  <div className="flex justify-between mb-2 mt-4">
                    <div
                      className={`flex items-center gap-1 cursor-pointer`}
                      onClick={() =>
                        this.resetReportContent(this.state.selectAll)
                      }
                    >
                      {this.state.selectAll ? (
                        <MdCheckBox className="w-6 h-6 text-blue-700" />
                      ) : (
                        <MdCheckBoxOutlineBlank className="w-6 h-6" />
                      )}
                      <span>Select all</span>
                    </div>

                    {this.state.waitingListToPrint && !this.state.loading && (
                      <ExportToExcel
                        fileData={this.state.waitingListToPrint}
                        fileName={this.state.reportTitle}
                        btnName="Generate"
                      />
                    )}
                  </div>
                </div>
              </Modal>
            )}
          {/* END GENERATE DYNAMIC REPORT CONTENT MODAL */}
        </section>
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  system_settings,
  sessionPlan,
  schoolLevels,
  waitingList,
}: StoreState): {
  auth: Auth;
  application_sessions: SessionPlanInterface[] | null;
  systemPositions: PositionAPI[] | null;
  schoolLevels: schoolLevelsStoreInterface;
  waitingList: WaitingListInterface[] | null;
} => {
  return {
    auth: auth,
    application_sessions: sessionPlan.allSessionPlans,
    systemPositions: system_settings.systemPositions,
    schoolLevels,
    waitingList,
  };
};

export const WaitingListReport = connect(mapStateToProps, {
  getWaitingList,
  GetAllSessionPlans,
  loadEducationCategories,
  getPositions,
  PushPopupAlert,
})(_WaitingListReport);
